.btnDG{
    display: block;
        
        height: 50;
        border-radius: 25px;
        margin: 1rem 0;
        font-size: 1.2rem;
        outline: none;
        border: none;
        background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
        color: #fff;
        cursor: pointer;
        background-size: 200%;
        transition: .5s;
        text-transform: uppercase;

        &:hover {
          background-position: right;
        }
}

