//tag
.tag-field {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  height: 50px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;  
}

input {
  border: 0;
  outline: 0;
}

.tag {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
          align-items: center;
  height: 35px;
  margin-right: 5px;
  padding: 0 8px;
  color: #fff;
  background: #47CF73;
  border-radius: 15px;
  cursor: pointer;
}

.tag-close {
  display: inline-block;
  margin-left: 0;
  width: 0;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
  overflow: hidden;
}

.tag:hover .tag-close {
  margin-left: 10px;
  width: 10px;
}