//Login

.wave {
  position: fixed;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.containerLogin {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 7rem;
  padding: 0 2rem;
  transition: .3s;

  .img {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 500px;
    }
  }

  .login-container {
    display: flex;
    align-items: center;
    text-align: center;

    .avatar {
      width: 100px;
    }

    form {
      width: 360px;

      h2 {
        font-size: 2.9rem;
        text-transform: uppercase;
        margin: 15px 0;
        color: #333;
      }

      .input-div {
        position: relative;
        display: grid;
        grid-template-columns: 7% 93%;
        margin: 25px 0;
        padding: 5px 0;
        border-bottom: 2px solid #d9d9d9;
        transition: .3s;

        &:after,
        &:before {
          content: '';
          position: absolute;
          bottom: -2px;
          width: 0%;
          height: 2px;
          background-color: #38d39f;
          transition: .4s;
        }

        &.focus {

          
          &:after {
            right: 50%;
          }
  
          &:before {
            left: 50%;
          }

          &:after,
          &:before {
            width: 50%;
          }

          & .i i {
            color: #38d39f;
            transition: .3s;
          }

          & h5 {
            top: -5px;
            font-size: 15px;
          }
        }


        input {
          position: absolute;
          width: 100%;
          height: 100;
          top: 0;
          left: 0;
          border: none;
          background: none;
          outline: none;
          padding: .5rem .7rem;
          font-size: 1.2rem;
          color: #555;
        }


        .i {
          display: flex;
          justify-content: center;
          align-items: center;

          i {
            color: #d9d9d9;
          }

        }

        &>div {
          position: relative;
          height: 45px;
        }

        &>div h5 {
          position: absolute;
          left: 10px;
          top: 20px;
          transform: translateY(-50%);
          color: #999;
          font-size: 18px;
          transition: .3s;
        }
      }

      a {
        display: block;
        text-decoration: none;
        text-align: right;
        color: #999;
        font-size: .9rem;
        transition: .3s;

        &:hover {
          color: #38d39f;
        }
      }

      .btn {
        display: block;
        width: 100%;
        height: 50;
        border-radius: 25px;
        margin: 1rem 0;
        font-size: 1.2rem;
        outline: none;
        border: none;
        background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
        color: #fff;
        cursor: pointer;
        background-size: 200%;
        transition: .5s;
        text-transform: uppercase;

        &:hover {
          background-position: right;
        }
      }

    }
  }



}

@media screen and (max-width: 1300px) {
  .containerLogin {
    grid-gap: 2rem;
  }
}
@media screen and (max-width: 1050px) {
  .containerLogin {
    grid-gap: 5rem;
  }
}

@media screen and (max-width: 1000px) {
  form {
    width: 290px;

    h2 {
      font-size: 2.4rem;
      margin: 8px 0;
    }

    .img img {
      width: 400px;
    }
  }
}

@media screen and (max-width: 970px) {
  .wave {
    display: none;
  }
 
  .containerLogin {
    grid-template-columns: 1fr;

    .img {
      display: none;
    }

    .login-container{
      justify-content: center;
    }
  }

}